
$homepage:"/youtubePlayer";

$typograpy:(
    headline1:("h1",6rem, 6rem, -.015625em),
    headline2:("h2",3.75rem, 3.75rem, -.0083333333em),
    headline3:("h3",3rem, 3.125rem, normal),
    headline4:("h4",2.125rem, 2.5rem, .0073529412em),
    headline5:("h5",1.5rem, 2rem, normal),
    headline6:("h6", 1.25rem, 2rem, .0125em),
    subtitle1:("", 1rem, 1.75rem, .009375em),
    subtitle2:("", 1rem, 1.75rem, .009375em), 
    body1:("", 1rem, 1.5rem, .03125em),
    body2:("", .875rem, 1.25rem, .0178571429em),
    caption:("", .75rem, 1.25rem, .0333333333em),
    button:("", .875rem, 2.25rem, .0892857143em),
    overline:("", .75rem, 2rem, .1666666667em),
);


$theme-colors:(
    primary:#FFE600,
    //primary-light:#68F4FF,
    //primary-dark:#00909B,
    /*secoundary:#FFFF00,
    secoundary-light:#FFFF5A,
    gradation-diagonally:linear-gradient(230deg, #D8FFE3 7%, #FFA4CF 30.5%, #FCFF6C 55%, #E5AAF6 75%, #B5FFF2 95%),
    gradation-side:linear-gradient(270deg, #D8FFE3 0%, #FFA4CF 25%, #FCFF6C 50%, #E5AAF6 75%, #B5FFF2 100%),*/
    black:#121212,
    black87:rgba(0, 0, 0, 0.87),
    black60:rgba(0, 0, 0, 0.6),
    black38:rgba(0, 0, 0, 0.38),
    white100:#FFFFFF,
    white87:rgba(255, 255, 255, 0.87),
    white60:rgba(255, 255, 255, 0.6),
    white38:rgba(255, 255, 255, 0.38),
);


@mixin button(){
    width: 24px;
    height: 24px;
    border: 0;
    outline: 0;
    overflow: hidden;
    text-indent: -1000px;
}


@mixin formParts(){
    width:100%;
    height:3em;
    border:none;
    outline: none;
    border-bottom:1px solid rgba(255, 255, 255, 0.3);
    background: transparent;
    color: map-get( $theme-colors , white87 );
    font-size:1rem;
    &:focus{
        border-bottom:1px solid map-get($theme-colors, primary);
        color:map-get( $theme-colors , white100 );
    }
}
