@import "../_base";

.playlist {
  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    //margin-top: 16px;
    padding: 8px 0;
    position: relative;
    figure {
      width: 88px;
      margin-right: 12px;
      img {
        width: 88px;
        height: auto;
      }
    }
    /*&:active:before {
      content: "";
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: #000;
      opacity: 0.1;
    }*/
  }
  &__title {
    //width: calc( 100% - 24px - 88px -12px );
    display: flex;
    color: map-get($theme-colors, white100);
    flex: 1;
    align-items: center;
  }
  &__more {
    width: 24px;
    margin-left: 16px;
  }
}
