.searchbox-suggest {
  padding: 16px 0;
  color:map-get($theme-colors,white100);
  &__result {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__thumbnail {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    img {
      position: relative;
      width: auto;
      height: 60px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__title {
    width: calc(100% - 76px);
  }
}
