.l-dismessible {
    width: 100%;
    
    overflow: hidden;
    &__wrap{
      width:200%;
      display: flex;
      transition: all 0.3s;
      .current0 & {
        //transform: translateX(0);
        transition: all 0.3s;
      }
      .current1 & {
        margin-left: -100%;
        //transform: translateX(-50%);
        transition: all 0.3s;
      }
    }
    &__item {
      width: 100%;
    }
  }