@import "../base";

#youtubeplayer {
  > div {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
    iframe {
      position: absolute;
      top: 0;
      right: 0;
      width: 100% !important;
      height: 100% !important;
    }
  }
}

.player-title {
  color: map-get($theme-colors, white100);
  display: flex;
  justify-content: space-between;
  align-items: center;
  h5 {
    margin-right: 12px;
    font-size:3vh;
    height:3.5vh;
    line-height: 1.16;
    width: calc(100% - 40px);
    overflow: hidden;
    position: relative;
    span{
      position: absolute;
      display : inline-block;
      padding-left: 100%;
      white-space : nowrap;
      animation   : scrollAnime 15s linear infinite;
    }
    @keyframes scrollAnime{
        0% { transform: translateX(0)}
      100% { transform: translateX(-100%)}
    }
  }
}
