@import "./reset.scss";
@import "./_base";
//@import url('https://fonts.googleapis.com/css?family=Noto+Sans+JP&display=swap');

html {
  min-height: 100%;
  background: linear-gradient(180deg, #8aaaa8 0%, #1d2323 100%);
}

body {
  position: relative;
  //font-family: 'Noto Sans JP', sans-serif;
}

/*
* タイポグラフィ
*/
@each $tag, $typo in $typograpy {
  $classes: ".typo_" + $tag;

  @if nth($typo, 1) != "" {
    $classes: $classes + " ," + nth($typo, 1);
  }

  #{$classes} {
    font-size: nth($typo, 2);
    line-height: nth($typo, 3);
    letter-spacing: nth($typo, 4);
    font-weight: normal;
    //font-weight: nth($typo, 5);
  }
}

.font-reset {
  font-size: 0;
  line-height: 1;
}

.bold {
  font-weight: bold;
}

/*
* カラー
*/
@each $tag, $color in $theme-colors {
  $classes: ".color_" + $tag;

  #{$classes} {
    color: $color;
  }
}

/*
* レイアウト
*/
.l-wrap {
  //padding: 0 16px;
  margin-bottom: 54px;
}

.l-contwrap {
  padding: 0 16px;
}

.l-unit {
  margin-top: 16px;
}
.l-scroll {
  height: calc(100% - 54px);
  overflow-y: scroll;
  margin: 0 -16px;
  padding: 0 16px;
  position: fixed;
  box-sizing: border-box;
  width: 100%;
}

section {
  padding: 0 0 36px;
  &:first-child {
    padding-top: 36px;
  }
}

/*
* 配置
*/
//TOP位置のみfix
.fixtop {
  //@warn: absolute;
  //position: fixed;
  //@extend: absolute;
  top: 0;
  width: 100%;
  box-sizing: border-box;
}

/*
* react-tabs
*/
.react-tabs {
  width: 100%;
  //overflow: hidden;
  &__tab-list {
    position: fixed;
    left: 0;
    bottom: 0;
    background: #4a5b5a;
    width: 100%;
    display: flex;
    z-index: 9999;
  }
  li {
    height: 54px;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.bottomnav-icon {
  text-align: center;
  opacity: 0.6;
  .react-tabs__tab--selected & {
    opacity: 1;
  }
  &__img {
  }
  &__text {
    line-height: 0.8 !important;
    margin-bottom: 0.1rem;
    font-size: 0.7rem;
  }
}

/*
* ボタン
*/
.btn-back {
  @include button();
  background: url($homepage+"/images/icon_back.svg");
  text-indent: -1000%;
}

.more-button {
  @include button();
  background: url($homepage+"/images/icon_more_vert.svg");
  //opacity: 0.6;
}
.add-button {
  @include button();
  background: url($homepage+"/images/icon_add.svg");
}

/*
* フォーム部品
*/

select {
  @include formParts();
  option {
    color: map-get($theme-colors, black87);
  }
}

.input-text {
  position: relative;
  input {
    @include formParts();
    padding-right: 32px;
    box-sizing: border-box;
    &::placeholder {
      color: map-get($theme-colors, white60);
    }
  }
  &__close{
    display: none;
    position: absolute;
    @include button();
    background: url($homepage+"/images/icon_close.svg");
    right:0;
    top:50%;
    margin-top: -12px;
    &.view{
      display: block;
    }
  }

}
